import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-wizard-step-title';

const WizardStepTitle = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

WizardStepTitle.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

WizardStepTitle.defaultProps = {
};

WizardStepTitle.displayName = 'WizardStepTitle';

export default WizardStepTitle;
