import React, { forwardRef } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';
import Icon from '../icon';
import WizardStepError from '../wizard-step-error';

const CSS_PREFIX = 'vds-wizard-step';
const CSS_STATE = 'vds-state';

const WizardStep = forwardRef(({
  active,
  children,
  className,
  completed,
  error,
  titles,
  ...remainingProps
}, ref) => (
  <li
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--active`]: Boolean(active),
      [`${CSS_STATE}--completed`]: Boolean(completed),
      [`${CSS_STATE}--error`]: Boolean(error),
      [`${CSS_PREFIX}--titles`]: Boolean(titles)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
    {
      error ?
        <WizardStepError>
          <Icon name="error-alt" resolution="tiny" />
        </WizardStepError>
      : undefined
    }
  </li>
));

WizardStep.propTypes = {
  /**
  * Active
  */
  active: bool,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Completed
  */
  completed: bool,

  /**
  * Error
  */
  error: bool,

  /**
  * Title
  */
  titles: bool


};

WizardStep.displayName = 'WizardStep';

export default WizardStep;
